<template>
    <v-data-table
        :headers="headers"
        :items="items"
        :items-per-page="itemsPerPage"
        :class="[selectedClass]"
        :height="height"
        :loading="loading"
        :loading-text="loadingText"
        item-key="pro_code"
        v-model="selectedItem"
        @click:row="onRowClick"
        
    >

    </v-data-table>
</template>

<script>
export default {
    name: 'Table',
    props:{
        headers:{
            type: Array,
            default: ()=> []
        }, 
        
        items:{
            type: Array,
            default: ()=> []
        },

        loading: {
            type: Boolean,
            default: false
        },

        loadingText:{
            type: String,
            default: 'Loading... Please wait...'
        },

        itemsPerPage:{
            type: Number,
            default: 5
        },

        height: {
            type: Number,
            default: 400
        },
    },

    data(){
        return{
            selectedItem: [{pro_code: ""}]
        }
    },

    methods:{
        onRowClick(row){
            this.selectedItem = [{pro_code: row.pro_code}];
            this.$emit('selected', row);
        }
    },

    computed:{
        selectedClass(item){
            return item.selected ? 'selected' : '';
        }
    }

}
</script>
